export default class GameService {
  startGame () {
    const rtn = {}
    rtn.numbers = this.getNumbers()
    rtn.goal = this.getGoal(rtn.numbers)
    return rtn
  }

  getNumbers () {
    const smallnumbers = [1, 2, 3, 4, 5, 6, 7, 8, 9]
    const bignumbers = [10, 25, 50, 75, 100]
    const numbers = []
    let x
    for (let i = 0; i < 4; i++) {
      x = Math.floor(Math.random() * 8)
      numbers.push(smallnumbers[x])
    }
    for (let i = 0; i < 2; i++) {
      x = Math.floor(Math.random() * 4)
      numbers.push(bignumbers[x])
    }
    return numbers
  }

  getGoal (numbers) {
    let goal = 0
    while (goal < 101 || goal > 999) {
      const arr = JSON.parse(JSON.stringify(numbers))
      const arrI = [0, 1, 2, 3, 4, 5]
      for (let i = 0; i < 3; i++) {
        const x = Math.floor(Math.random() * (6 - i))
        goal = arr[arrI[x]]
        arrI.splice(x, 1)
        const y = Math.floor(Math.random() * (5 - i))
        goal = this.getOperator(goal, arr[arrI[y]])
        arrI.splice(y, 1)
        arrI.push(i + 6)
        arr.push(goal)
      }
      arrI.forEach(el => {
        if (el > 5 && el < arr.length - 1) {
          goal = 0
        }
      })
    }
    return goal
  }

  getOperator (n1, n2, op) {
    const ops = ['-', '+', 'x', '/']
    if (n1 / n2 - Math.floor(n1 / n2)) {
      ops.splice(3, 1)
    }
    if (n2 >= n1) {
      ops.splice(0, 1)
    }
    op = op || ops[Math.floor(Math.random() * ops.length)]
    switch (op) {
      case '-':
        return n1 - n2
      case '+':
        return n1 + n2
      case 'x':
        return n1 * n2
      case '/':
        return n1 / n2
    }
  }
}
